.skills-container {
  padding: 15px;
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 15px;
}

.skills-info-container {
  margin: 0 auto;
  width: 96%;
  --font-family: "Segoe UI", system-ui, Tahoma, Geneva, Verdana, sans-serif;
  --direction: ltr;
}
.skills-info-container * {
  font-family: system-ui, "Segoe UI", Tahoma, Geneva, Verdana, sans-serif !important;
  direction: ltr !important;
}

.skill-row {
  gap: 1vw;
  margin: 30px 0;
}

.skill-icon {
  font-size: x-large;
  width: 50px;
}
.skill-title {
  font-size: large;
  min-width: 100px;
  text-align: left;
}

.skill-progress-bar-value {
  height: 30px;
  background-color: #e9b508;
  transition: 1s;
  transform: translateX(-60vw);
  border-radius: 10pt;
  top: 0;
  position: absolute;
}

.skill-bar-show {
  transform: translateX(0);
}

.skill-progress-bar-container {
  border: 2px solid;
  border-radius: 20pt;
  position: relative;
  height: 30px;
  width: 80%;
  overflow: hidden;
}

@media only screen and (max-width: 768px) {
  .skill-progress-bar-container {
    height: 20px;
  }
  .skill-progress-bar-value {
    height: 20px;
  }
  .skills-info-container {
    max-width: 100%;
  }
}
