.contacts-info {
  gap: 3vh 2vw;
  margin: 30px 0vw;
}

.contact-icon {
  font-size: xxx-large;
  height: 60px;
  gap: 15px;
  transition: 0.2s;
}

.contact-sub-title {
  font-size: large;
  /*filter: drop-shadow(0px 2px 6px black);*/
  margin-top: 10px;
  /*white-space: nowrap;*/
}

.contact-title {
  font-size: xx-large;
  font-weight: bold;
  margin-top: 5px;
  transition: 0.15s;
}



@media only screen and (max-width: 768px) {
  .contact {
    min-width: 160px;
  }
  .contacts-info {
    margin-left: 0vw;
  }
}
