.projects-container {
  width: 100%;
  height: calc(100vh - 70px);
  box-sizing: border-box;
  padding: 0;
  gap: 0;
}
.projects-container-fa {
  direction: rtl;
  font-family: persian;
}

.charts-container {
  width: 100%;
  gap: 2vh 2.5%;
  justify-content: space-evenly;
  padding: 60px 1vw 15px;
  overflow-y: auto;
  box-sizing: border-box;
  position: relative;
}

.chart {
  text-align: justify;
  --border: 1px solid;
  box-sizing: border-box;
  border-radius: 12pt;
  padding: 1vh 1vw;
}

.large-chart {
  min-width: 100%;
  flex: 1 0 9%;
  text-align: justify;
  font-size: large;
}

.medium-chart {
  width: 40%;
  flex: 1 0 40%;
}
.medium-chart-without-width {
  flex: 1 0 40%;
}

.small-chart {
  flex: 1 0 30%;
  width: 30%;
}

.chart-container {
  /*max-width: 30vw;
  width: 100%;
  display: flex;
  justify-content: center;*/
  margin: auto;
  display: flex;
  --justify-content: center;
  max-height: 44vh;
  margin: 0 5%;
}

.medium-chart-without-width .chart-container {
  max-height: 44vh;
}
.small-chart .chart-container {
  max-height: 30vh;
}

.chart-name {
  font-size: larger;
  font-weight: 700;
  height: 7%;
  min-height: 7%;
  margin-bottom: 1.5%;
}

.project-list-selection {
  padding: 15px 5px;
  width: 90%;
  box-sizing: border-box;
  margin: 0 auto 5px;
  border-radius: 7pt;
}

/*project selection */
.project-list-container {
  width: 30vw;
  min-width: 300px;
  height: 100%;
  box-sizing: border-box;
  /*padding-top: 50px;*/
  border-right: 1px solid;
  border-left: 1px solid;
  text-align: left;
}
.projects-container-fa .project-list-container {
  text-align: right;
}

.project-detail-reset-filter-btn {
  height: 50px;
  box-sizing: border-box;
  padding: 15px 30px;
}
.project-detail-reset-filter-btn span {
  cursor: pointer;
  color: rgb(127, 40, 209);
}
.dark .project-detail-reset-filter-btn span {
  color: #e9b508;
}

.project-list {
  width: 100%;
  height: calc(100vh - 120px);
  overflow-x: hidden;
  overflow-y: auto;
  --transition: 0.2s;
}
.project-list h3 {
  padding: 20px 2vw;
  cursor: pointer;
  font-size: 1.1em;
}
.light .project-list h3:nth-child(odd) {
  background-color: #c0c0c0;
}

.dark .project-list h3:nth-child(odd) {
  background-color: #6e6e6e;
}
.project-list h3:not(.selected-project):hover {
  scale: 1.01;
  background-color: #a1a1a1 !important;
}

.selected-project {
  background-color: #e9b508 !important;
  --position: sticky;
  --top: 0;
}

/* Project info part */
.project-data-container {
  gap: 5vh;
  width: 100vw;
  box-sizing: border-box;
}
.project-data-container h1 {
  font-size: xxx-large;
  font-weight: bolder;
}

.explanation-container {
  gap: 2vw;
  min-height: 40vh;
}

.project-data-box {
  position: relative;
  border-radius: 12pt;
  box-sizing: border-box;
  padding: 2px 5px 5px;
}
.light .project-data-box {
  background-color: #c5c5c5;
  --filter: drop-shadow(0px 1px 1px black);
}
.dark .project-data-box {
  background-color: rgb(50, 50, 50);
  --filter: drop-shadow(0px 1px 1px white);
}
.project-data-box * {
  position: relative;
  z-index: 1;
}

.project-explanation {
  box-sizing: border-box;
  padding: 10px 15px;
  text-align: justify;
  --border: 1px solid;
  border-radius: 18pt;
}
.project-explanation h2 {
  font-size: x-large;
  font-weight: bold;
  margin-bottom: 15px;
}

.project-description {
  flex: 1 0 60%;
}
.project-description p {
  margin-bottom: 7px;
}

.project-technologies {
  flex: 1 0 30%;
  gap: 15px;
  align-content: flex-start;
  min-width: 230px;
}
.project-technologies h2 {
  flex: 1 0 100%;
}

.technology-list {
  width: 100%;
  gap: 2vh 1vw;
}

.project-techs-icon-container {
  width: 70px;
  border: 1px solid;
  border-radius: 18pt;
  padding: 0 5px;
  border-radius: 12pt;
  height: 90px;
  justify-content: center;
}
.project-techs-icon {
  font-size: x-large;
}

.kpi-container {
  gap: 1vw;
}

.project-kpi {
  --border: 1px solid;
  border-radius: 18pt;
  flex: 1 0 20%;
  gap: 2vh;
  box-sizing: border-box;
  padding: 15px 5px;
  min-width: 230px;
  min-height: 25vh;
}
.project-kpi h3 {
  text-align: justify;
  font-size: 0.9em;
  padding: 0 7px;
  box-sizing: border-box;
}
.project-kpi h1 {
  font-size: xx-large;
  font-weight: bold;
  margin: auto;
}

.full-kpi {
  flex: 1 0 25% !important;
}
.full-kpi div {
  margin: auto;
  display: flex;
  flex-direction: column;
  gap: 2vh;
}

@media only screen and (max-width: 969px) {
  .chart {
    max-width: 100%;
  }
  .chart-container,
  .medium-chart-without-width .chart-container {
    max-height: 100vw;
  }

  .medium-chart-without-width {
    flex: 1 0 100%;
  }

  .medium-chart,
  .small-chart {
    flex: 1 0 100%;
    width: 100%;
  }

  .medium-chart-without-width .chart-container {
    max-height: 100vw;
  }
  .small-chart .chart-container {
    max-height: 100vw !important;
  }
}

@media only screen and (max-height: 870px) {
  .medium-chart-without-width .chart-container {
    max-height: 100vw;
  }
  .small-chart .chart-container {
    max-height: 25vh;
  }
}

@media only screen and (max-width: 768px) {
  .medium-chart-without-width .chart-container {
    height: 100vw;
  }
  .small-chart .chart-container {
    height: 100vw !important;
  }

  .charts-container {
    padding: 10px 1vw 50px;
    padding-bottom: 5vh;
    border-top: 1px solid;
  }
  .projects-container {
    flex-direction: column;
    padding-top: 50px;
  }

  .chart-container canvas {
    width: 100% !important;
    --height: auto !important;
  }

  .chart:not(:first-child) {
    max-width: 100%;
  }

  /*Project info part*/
  .project-data-container {
    gap: 2vh;
  }
  .project-data-container h1 {
    font-size: xx-large;
  }

  .explanation-container {
    padding: 15px 5px;
    gap: 15px;
  }
  .project-explanation {
    min-height: fit-content;
  }
  .project-description {
    flex: 1 0 100%;
  }
  .project-technologies {
    flex: 1 0 100%;
  }

  .kpi-container {
    gap: 1vh 1%;
  }

  .project-kpi {
    flex: 1 0 49%;
    gap: 30px;
    padding-bottom: 45px;
  }
  .project-kpi h1 {
    margin-bottom: 20px;
  }

  .full-kpi {
    flex: 1 0 100% !important;
  }
}

