.education-container {
  background-color: #757884;
  margin-top: 15px;
  padding: 15px;
  box-sizing: border-box;
  text-align: justify;
}

.education-container .timeline-container::after {
  margin-left: calc(5vw + 70px);
}

.education-container .time-line-item-container::after {
  margin-left: 58.5px;
}

.education-container
  .right-timeline {
  left: 20px;
}

@media only screen and (max-width: 768px) {
  .education-container .timeline-container::after {
    margin-left: 70px;
  }
}
