.timeline-container {
  position: relative;
  top: 30px;
  width: 100%;
  margin-bottom: 45px;
  padding: 0 5vw;
  box-sizing: border-box;
}

.timeline-container::after {
  content: "";
  position: absolute;
  width: 5px;
  background-color: black;
  top: 0;
  bottom: 0;
  left: 0;
  margin-left: calc(5vw + 100px);
}
.about-me-container-fa .timeline-container::after {
  margin-right: calc(5vw + 100px);
  margin-left: 0 !important;
  right: 0 !important;
}


.time-line-item-container {
  padding: 10px 0;
  position: relative;
  background-color: inherit;
  width: 100%;
  display: flex;
}

.time-line-item-container::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: white;
  border: 4px solid #e9b508;;
  top: 15px;
  border-radius: 50%;
  z-index: 1;
  top: 30px;
  left: 0;
  margin-left: 89px;
}
.about-me-container-fa .time-line-item-container::after {
  margin-right: 89px;
  margin-left: 0 !important;
  right: 0 !important;
}


.left-timeline {
  position: relative;
  left: 0;
  display: inline-block;
  margin: 17px 0;
  font-size: large;
  transform: translateX(-100vw);
  transition: transform 0.7s;
}
.about-me-container-fa .left-timeline {
  transform: translateX(100vw);
}
.left-timeline div {
  width: 80px;
  margin: -10px 0;
  text-align: justify;
  gap: 8px 4px;
}
.left-timeline div:after {
  content: "";
  display: inline-block;
  width: 100%;
}
.left-time-line-icons {
  font-size: x-large;
}


.right-timeline {
  position: relative;
  left: 50px;
  display: inline-block;
  text-align: justify;
  padding: 0px 25px 0 0;
  box-sizing: border-box;
  margin: 7px 0;
  transform: translateX(100vw);
  transition: transform 1s;
}
.about-me-container-fa .right-timeline {
  right: 50px !important;
  padding: 0px 0 0 25px !important;
  transform: translateX(-100vw);
}
.timeline-title {
  text-align: left;
  font-size: xx-large;
}
.timeline-sub-title {
  font-size: x-large;
}
.timeline-content {
  font-size: larger;
}


.timeline-show {
  transform: translateX(0) !important;
}


@media only screen and (max-width: 768px) {
  .timeline-container {
    padding-right: 6vw;
    padding-left: 0;
  }
  .timeline-container::after {
    margin-left: 100px;
  }
  .right-timeline {
    left: 45px;
  }
  .timeline-title {
    font-size: larger;
  }
  .timeline-sub-title {
    font-size: medium;
  }
  .timeline-content {
    font-size: small;
  }

  .about-me-container-fa .right-timeline {
    right: 45px !important;
    padding: 0px 0 0 45px !important;
  }
}