.portfolio-container {
  width: 100vw;
  height: calc(100vh - 70px);
}

.portfolio-info {
  width: 100vw;
  height: 75%;
}

.portfolio-introduction {
  user-select: none;
  box-sizing: border-box;
  cursor: context-menu;
  width: 50%;
  transition: scale 0.5s;
}
.portfolio-introduction-en {
  padding-left: 10vw;
  text-align: left;
}
.portfolio-introduction-fa {
  padding-right: 10vw;
  text-align: right;
}
.portfolio-introduction-fa p {
  margin: 5px 0;
  font-family: persian;
}
.portfolio-introduction-fa .portfolio-name {
  font-weight: 600;
}
.portfolio-introduction:hover {
  scale: 1.05;
}

.portfolio-hello {
  font-size: 3.5vw;
}
.portfolio-name {
  font-size: 4.5vw;
  margin-bottom: 15px;
}
.portfolio-position {
  font-size: 2.5vw;
}

/* Avatar Img */
.portfolio-img-container {
  height: 100%;
  width: 50%;
  justify-content: center;
  transition: 0.5s;
}

.portfolio-avatar {
  width: 90%;
  max-width: 30vw;
  transition: 0.5s;
}
.portfolio-img-container:hover {
  scale: 1.05;
}


/** Symbols **/
/* icons */
.portfolio-symbol {
  width: 100%;
  height: 25%;
}

.portfolio-contacts {
  width: 50%;
  box-sizing: border-box;
  padding: 0 10vw;
  height: 20vh;
  align-items: center;
}

.glass-icon {
  border-radius: 50%;
  width: 70px;
  height: 70px;
  font-size: x-large;
  padding-top: 5px;
  box-sizing: border-box;
  backdrop-filter: blur(2px);
  opacity: 0.8;
  --box-shadow: 0px 0px 10px;
  text-decoration: none;
  overflow: hidden;
  transition: 200ms !important;
  --border: 1px solid;
}
.glass-icon * {
  transition: 0ms !important;
}
.glass-icon:hover {
  transform: translateY(-10px);
  opacity: 1;
  font-size: xx-large;
}

.portfolio-btn-container {
  flex: 1 0 50%;
  align-items: center;
  gap: 20px 5vw;
  box-sizing: border-box;
  padding: 0 1vw;
  height: 20vh;
  flex-flow: column;
}


@media only screen and (max-width: 768px) {
  .portfolio-info {
    flex-direction: column-reverse;
    height: 70%;
  }
  .portfolio-introduction {
    width: 100%;
  }
  .portfolio-introduction-en, .portfolio-introduction-fa {
    padding: 0;
    text-align: center;
    height: 50%;
  }
  .portfolio-hello {
    font-size: 3.5vh;
  }
  .portfolio-name {
    font-size: 5vh;
  }
  .portfolio-position {
    font-size: 2.5vh;
  }


  .portfolio-img-container {
    width: 100%;
    height: 50%;
    min-height: 30%;
  }
  .portfolio-avatar {
    --min-width: 60vw;
    max-height: 100%;
    height: 100%;
    max-width: 100%;
    width: auto;
  }

  .portfolio-symbol {
    height: 30%;
    flex-direction: column-reverse;
    justify-content: center;
  }

  .portfolio-contacts {
    height: 50%;
  }

  .portfolio-contacts {
    --height: calc(100% - 70px);
    align-items: center;
    justify-content: center;
    width: 100%;
}
}