.project-filter-container {
  position: relative;
  width: 100%;
  margin-bottom: 50px;
}

.project-filter-title {
  position: absolute;
  top: -17px;
  z-index: 3;
  left: 8%;
  width:fit-content;
  box-sizing: border-box;
  padding: 0 5px;
  background-color: #c0c0c0;
  transition: background-color 0.5s;
}
.project-filter-title-fa {
  right: 8% !important;
}
.dark .project-filter-title {
  background-color: #6e6e6e;
}

.project-list-selection {
  padding: 15px 5px;
  background-color: #c0c0c0;
  border: 1px solid;
  border-radius: 7pt;
  transition: 0.5s;
}
.dark .project-list-selection {
  background-color: #6e6e6e;
  color: #ededed
}