.portfolio-next-page {
  height: 50px;
  gap: 15px;
  transition: border 0.5s, border-radius 0.5s;
  cursor: pointer;
  font-weight: 600;
  margin: 0 auto;
  --background-color: #e9b508;
  width: 230px;
  position: relative;
  box-sizing: border-box;
  border: 0px solid;
}
.portfolio-next-page-btn {
  z-index: 2;
  margin: 0 auto;
  padding: 5px 15px;
}
.next-page-btn-horizental {
  content: "";
  position: absolute;
  width: 230px;
  height: 60px;
  top: -6px;
  border: 1px solid;
  transition: transform 0.6s;
  z-index: 1;
  border-radius: 9pt;
}
.next-page-btn-vertical {
  content: "";
  position: absolute;
  width: 240px;
  height: 50px;
  left: -6px;
  border: 1px solid;
  transition: transform 0.6s;
  z-index: 1;
  border-radius: 9pt;
}
.portfolio-next-page:hover {
  --scale: 1.01;
  border: 1px solid;
  border-radius: 9pt;
}
.portfolio-next-page:hover .next-pagebtn-text {
  scale: 1.02;
}
.portfolio-next-page:hover .next-page-btn-horizental {
  border-left: none;
  border-right: 0px solid;
  transform: scaleX(0);
  border-radius: 0;
}
.portfolio-next-page:hover .next-page-btn-vertical {
  border-top: none;
  border-bottom: none;
  transform: scaleY(0);
  border-radius: 0;
}


@media only screen and (max-width: 1000px) {
  .portfolio-next-page {
    height: 50px;
  }
}