.project-number {
  position: absolute;
  right: 1vw;
  font-size: large;
  padding: 20px;
  box-sizing: border-box;
  width: fit-content;
  min-width: 34%;
  height: 50px;
  border-radius: 12pt;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: scale 200ms;
  cursor: pointer;
  top: 5px;
  background-color: #a1a1a1;
  color: white;
  gap: 15px;
}
.dark .project-number {
  color: black;
  background-color: #cfcfcf;
}


.projects-container-fa .project-number {
  right: calc(66% - 1vw); 
}


.filters-container {
  z-index: 1;
  min-width: 300px;
  width: 30%;
  background-color: #c0c0c0;
  color: black;
  transition: 0.5s;
  display: flex;
  flex-direction: column;
  position: absolute;
  justify-content: flex-start;
  top: 0;
  overflow: hidden;
  border-bottom: 1px solid;
  border-right: 1px solid;
  border-left: 1px solid;
  border-bottom-left-radius: 12pt;
  border-bottom-right-radius: 12pt;
  user-select: none;
  left: 1vw;
}
.dark .filters-container {
  background-color: #6e6e6e;
  color: white
}
.filters-container h2 {
  font-size: 1.2em;
}
.projects-container-fa .filters-container {
  left: calc(70% - 1vw)
}

.filters-header {
  min-height: 25px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0 50px;
  gap: 15px;
  cursor: pointer;
  width: 100%;
  margin-bottom: 30px;
  user-select: none;
}

.filter-header-arrow {
  position: absolute;
  right: 15px;
  height: 50px;
  display: flex;
  align-items: center;
  transition: rotate 300ms;
  font-size: 1.1em;
}
.projects-container-fa .filter-header-arrow {
  left: 15px !important;
  right: unset;
}


.reset-filters-btn {
  color: rgb(103, 55, 148);
  width: 100px;
  padding: 0 7%;
  margin-top: -22px;
  font-size: large;
  transition: 0.5s;
}
.reset-filters-btn * {
  cursor: pointer;
}
.dark .reset-filters-btn {
  color: #e9b508
}



/** Charts **/
.double-width-chart {
  flex: 1 0 60%;
  min-width: 450px;
  max-height: 48%;
  padding: 1vh 1vw 2vh !important;
  position: relative;
  box-sizing: border-box;
  box-shadow: 0 0 3px;
}

.simple-chart {
  flex: 1 0 30%;
  max-height: 48%;
  padding: 1vh 1vw 2vh !important;
  position: relative;
  box-sizing: border-box;
  box-shadow: 0 0 3px;
}

.second-line-chart {
  flex: 1 0 25%;
  max-height: 48%;
  padding: 1vh 1vw 2vh !important;
  position: relative;
  box-sizing: border-box;
  box-shadow: 0 0 3px;
}

/*Bar Chart*/
.bar-chart-container {
  min-width: 100%;
  min-height: 100%;
}

.bar-chart {
  min-height: 75%;
  max-height: 85%;
  display: flex;
  justify-content: center;
  margin-top: 15px;
}

/*Doughnut Chart*/
.doughnut-chart-container {
  min-width: 100%;
  min-height: 100%;
}

.doughnut-chart {
  max-height: 90%;
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 15px;
}


/*Label for tech*/
.most-used-tech-container {
  position: absolute;
  background-color: #a1a1a1;
  color: white;
  bottom: -10px;
  height: 50px;
  width: fit-content;
  left: -10px;
  padding: 5px 15px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom-left-radius: 24pt;
  border-top-right-radius: 24pt;
  border-bottom-right-radius: 5pt;
  border-top-left-radius: 5pt;
  box-shadow: 1px -1px 4px black;
}
.dark .most-used-tech-container {
  color: #ffffff;
  background-color: #414040;
  box-shadow: 1px -1px 4px white;
}
