.pageContainer {
  width: 100vw;
  min-height: 100vh;
  background-color: #181446;
  cursor: context-menu;
}

.space {
  width: 50vw;
  overflow: hidden;
}

.moon {
  background-color: #39beff;
  height: 170px;
  width: 170px;
  border-radius: 50%;
  overflow: hidden;
}

.crater {
  background-color: #31b4ff;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  position: relative;
}
.crater::before {
  content: "";
  position: absolute;
  height: 25px;
  width: 25px;
  border-radius: 50%;
  box-shadow: -5px 0 0 2px #1ca4f9;
  top: 2px;
  left: 7px;
}

.crater-1 {
  top: 30px;
  left: 95px;
  transform: scale(0.9);
}
.crater-2 {
  top: -15px;
  left: 61px;
  transform: scale(0.6);
}
.crater-3 {
  left: 15px;
  transform: scale(0.75);
}
.crater-4 {
  top: 24px;
  left: 111px;
  transform: scale(1.18);
}
.crater-5 {
  top: -8px;
  left: 31px;
  transform: scale(0.65);
}

.moon-shadow {
  height: 190px;
  width: 190px;
  box-shadow: 21px 0 0 5px rgba(0, 0, 0, 0.15);
  border-radius: 50%;
  position: relative;
  bottom: 157.5px;
  right: 46px;
}

.orbit {
  height: 280px;
  width: 280px;
  border-radius: 50%;
  position: absolute;
  animation: spin 5s infinite linear;
}
@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}

.rocket {
  background-color: #fafcf7;
  height: 50px;
  width: 25px;
  border-radius: 50% 50% 0 0;
  position: relative;
  left: -11px;
  top: 115px;
}
.rocket::before {
  position: absolute;
  content: "";
  background-color: #39beff;
  height: 20px;
  width: 55px;
  z-index: -1;
  border-radius: 50% 50% 0 0;
  right: -15px;
  bottom: 0;
}
.rocket::after {
  position: absolute;
  content: "";
  background-color: #39beff;
  height: 4px;
  width: 15px;
  border-radius: 0 0 2px 2px;
  left: 4.3px;
  bottom: -4px;
}

.rocket-window {
  height: 10px;
  width: 10px;
  background-color: #151845;
  border: 2px solid #b8d2ec;
  border-radius: 50%;
  position: relative;
  top: 17px;
  left: 5px;
}

.error-container {
  width: 50%;
  height:50vh;
  margin: auto;
  box-sizing: border-box;
  color: #fafcf7;
  padding: 0 15px;
}
.error-container h1 {
  font-size: 2.3rem;
  font-weight: 800;
}
.error-container p {
  font-size: 1.2rem;
}
.error-container h3 {
  font-size: 1.3rem;
}
.error-container a {
  color: #ababab;
}

.lang-selection {
  width: 100%;
}


@media only screen and (max-width: 768px) {
  .space {
    width: 100vw;
    height: 50vh;
  }
  .error-container {
    width: 100vw;
    align-self: flex-start;
    margin: 0;
  }
  .error-container h1 {
    margin-bottom: 30px;
  }
  .lang-selection h3 {
    width: 100%;
    margin: 7.5px 0;
  }
}
